    reach: faker.datatype.number({ min: 100, max: 5000, precision: 1 }),
    targetReach: faker.datatype.number({ min: 100, max: 5000, precision: 1 }),
    brandShared: faker.datatype.number({ min: 100, max: 5000, precision: 1 }),
    entries: faker.datatype.number({ min: 100, max: 5000, precision: 1 }),
    followingUsersNotCompleted: faker.datatype.number({ min: 100, max: 5000, precision: 1 }),
